[showBorders4] .KnFormFieldRow__inner > * {
  outline: 1px solid rgba(255, 0, 0, 0.5);
}
.KnFormFieldRow {
  position: relative;
}
.KnFormFieldRow__inner {
  display: grid;
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  grid-template-columns: repeat(12, 1fr);
}
.KnFormFieldRow__inner > * {
  min-height: 0;
  min-width: 0;
}
.KnFormFieldRow {
  border-top: 1px solid rgba(0, 55, 139, 0.15);
  border-bottom: 1px solid rgba(0, 55, 139, 0.15);
  background-color: white;
  margin-bottom: -1px;
}
.KnFormFieldRow__inner {
  padding-top: 12px;
  padding-bottom: 12px;
}
